.realtime_visitor ul{height:calc(100% * 4);animation:slide 12s infinite; width: 309px; padding-left: 0px;}
.realtime_visitor li{height:calc(100% / 4);}
.realtime_visitor li:nth-child(1){background:#ffa;}
.realtime_visitor li:nth-child(2){background:#faa;}
.realtime_visitor li:nth-child(3){background:#afa;}
.realtime_visitor li:nth-child(4){background:#aaf;}

    @keyframes slide {
      0% {margin-top:0;}
      10% {margin-top:0;}
      25% {margin-top:-52px;}
      35% {margin-top:-52px;}
      50% {margin-top:-104px;}
      60% {margin-top:-104px;}
      75% {margin-top:-156px;}
      85% {margin-top:-156px;}
      100% {margin-top:0;}
    }
