.range {
  position: absolute;
  top: 60px;
  width: 90%;
  margin-left: 5%;
  height: 80px;
  background-color: #fff;
  border-radius: 10px;
}
.range .slidervalue {
  position: relative;
  width: 100%;
}
.range .slidervalue span {
  position: absolute;
  height: 25px;
  width: 25px;
  color: #fff;
  font-weight: 500;
  top: -5px;
  left: 50px;
  transform: translateX(-50%);
  line-height: 55px;
  z-index: 7;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}
.range .slidervalue span:after {
  position: absolute;
  content: "";
  height: 25px;
  width: 25px;
  background-color: #5d6ef0;
  left: 50%;
  transform: translateX(-50%) rotate(45deg);
  border: 3px solid #fff;
  z-index: -1;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  border-bottom-left-radius: 50%;
}
.range .field {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.range .field .value {
  position: absolute;
  font-size: 18px;
  font-weight: 600;
  color: #5a6dfc;
  z-index: 7;
}
.left {
  font-size: 13px;
  position: absolute;
  left: 20px;
  display: flex;
  font-weight: bold;
  color: black;
}
.right {
  font-size: 13px;
  position: absolute;
  font-weight: bold;
  right: 18px;
  color: black;
}
.controlbox_range input {
  -webkit-appearance: none;
  height: 8px;
  width: 73%;
  background-color: #ddd;
  border-radius: 5px;
  outline: none;
  border: none;
  z-index: 7;
}
.controlbox_range input::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 14px;
